<template>
  <div class="p5FluidResistance">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null,
      movers: new Array(25),
      myLiquid: null
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);
        p.background(255);

        for (let i = 0; i < this.movers.length; i++) {
          this.movers[i] = new Mover(p.random(0.5, 5), p.random(0, p.width), 0);
        }

        this.myLiquid = new Liquid(0, p.height / 2, p.width, p.height / 2, 0.1);
      };

      p.draw = () => {
        p.background(255);

        this.myLiquid.display();

        for (let i = 0; i < this.movers.length; i++) {
          if (this.movers[i].isInside(this.myLiquid)) {
            this.movers[i].drag(this.myLiquid);
          }

          let m = 0.1 * this.movers[i].mass;
          let gravity = p.createVector(0, m);

          this.movers[i].applyForce(gravity);

          this.movers[i].update();
          this.movers[i].display();
          this.movers[i].checkEdges();
        }
      };

      class Mover {
        constructor(m, x, y) {
          this.location = p.createVector(x, y);
          this.velocity = p.createVector(0, 0);
          this.acceleration = p.createVector(0, 0);
          this.mass = m;
        }

        applyForce(force) {
          let f = P5.Vector.div(force, this.mass);
          this.acceleration.add(f);
        }

        update() {
          this.velocity.add(this.acceleration);
          this.location.add(this.velocity);
          this.acceleration.mult(0); // clear the acceleration each time
        }

        display() {
          p.stroke(0);
          p.fill("#42b983");
          p.ellipse(
            this.location.x,
            this.location.y,
            this.mass * 16,
            this.mass * 16
          );
        }

        isInside(liquid) {
          if (
            this.location.x > liquid.x &&
            this.location.x < liquid.x + liquid.w &&
            this.location.y > liquid.y &&
            this.location.y < liquid.y + liquid.h
          ) {
            return true;
          } else {
            return false;
          }
        }

        drag(liquid) {
          let speed = this.velocity.mag();
          let dragMagnitide = liquid.c * speed * speed;

          let drag = this.velocity.copy();
          drag.mult(-1);
          drag.normalize();
          drag.mult(dragMagnitide);
          this.applyForce(drag);
        }

        checkEdges() {
          if (this.location.y > p.height - this.mass * 8) {
            // reverse the direction of our object when it reaches the bottom.
            this.velocity.y *= -1;
            this.location.y = p.height - this.mass * 8;
          }
        }
      }

      class Liquid {
        constructor(x, y, w, h, c) {
          this.x = x;
          this.y = y;
          this.w = w;
          this.h = h;
          this.c = c;
        }

        display() {
          p.noStroke();
          p.fill("#AADAFF");
          p.rect(this.x, this.y, this.w, this.h);
        }
      }

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>

<style lang="scss" scoped>
.canvasP5 {
  display: block;
  overflow: hidden;
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
}
.p5FluidResistance {
  user-select: none;
}
</style>
